* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.container {
  max-width: 700px;
  margin: 0 auto;
  padding: 20px;
  max-width: 1200px;
}


.navbar-brand{
  font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  font-weight: 600;
  font-size:20px;
}

.bg-green{
  background-color: rgb(13, 150, 13);
}
.bg-red{
  background-color: rgb(219, 16, 16);
}
.bg-blue{
  background-color: #77a5d6;
}


.product-selector{
  margin-top: 40px;
  display: flex;
  justify-content:space-between;
  background-color: #f2f1f1;
  border-radius: 15px;
  padding: 20px;
  align-items:center;

}

.product-selector .mb-3{
   margin-top: 10px;
}

input{
  border-radius: 0.5px;
}


.form-btn{
  border-radius: 20px;
  padding: 5px 20px;
}

.btn-lite-add{
  background-color: rgb(181, 206, 181);
}

.btn-lite-add:hover{
  background-color: rgb(181, 206, 181);
}


.btn-lite-delete{
  background-color: rgb(227, 215, 134);
}

.btn-lite-delete:hover{
  background-color: rgb(227, 215, 134);
}

 .cart-table{
  margin-top: 20px;
 }

 .cart-table{
  margin-top: 30px;
 }

 .table-quantity{
  border-radius: 5px;
 }



/* Header Desgin */

.navbar {
  padding: 10px 0;
  border-radius: 0;
}

.navbar-brand {
  font-size: 28px;
  font-weight: bold;
}

.navbar-toggler-icon {

}


.nav-link {
  font-size: 18px;
  color: #000000; /* Black text */
  transition: background-color 0.3s ease-in-out;
  margin-left: 10px;
}

.nav-link:hover {
  text-decoration: underline #66a675 3px;
  text-underline-offset: 4px;
 
}

.dropdown-menu {
 
  border: none;
}

.dropdown-item {
  color: #000000; /* Black text */
  transition: background-color 0.3s ease-in-out;
}

.dropdown-item:hover {
  text-decoration: underline #66a675 3px;
  text-underline-offset: 4px;
}

.search-bar {
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 5px 10px;
  color: #000000; /* Black text */
  border-radius: 10px;
}

.search-btn {
  background-color: #66a675; /* Green button */
  color: #fff; /* White text */
  border: none;
  border-radius: 5px;
  margin-left: 10px;
  cursor: pointer;
  border-radius: 10px;
}

.search-btn:hover{
  background-color: green;
}

.nav-item.active .nav-link {
  background-color: #333; /* Darker background for active link */
}



/* Cart Table Design */

.cart-table {
  width: 100%;
  margin-top: 20px;
  border-collapse: collapse;
}

.cart-table th {
  background-color: #f2f2f2;
  padding: 10px;
  text-align: left;
  border: 1px solid #ddd;
}

.cart-table td {
  padding: 10px;
  border: 1px solid #ddd;
}

.table-quantity {
  width: 50px;
  padding: 5px;
  border: 1px solid #ddd;
  border-radius: 4px;
}

.btn-danger {
  background-color: #dc3545;
  color: #fff;
  border: none;
  padding: 5px 10px;
  border-radius: 4px;
  cursor: pointer;
}

.cart-table tr:last-child {
  font-weight:bolder;
  background-color: #333;
  color: #fff;
}

.table-group-divider::before {
  content: "";
  display: block;
  height: 10px;
}

